<template>
  <div>
    <div class="head-background" ref="headBackground">
      <strong style="font-size: 50px">火车</strong>
    </div>
    <div v-loading="loading" style="overflow: auto; height: 900px">
      <el-row type="flex" justify="center" class="search-nav">
        <el-col :xs="20" :sm="18" :md="16" :lg="12" :xl="12">
          <el-card
            shadow="always"
            class="search-nav-el-card"
            :body-style="{ padding: 0 }"
            style="position: relative; overflow: visible"
          >
            <el-row
              type="flex"
              justify="space-around"
              align="middle"
              style="height: 100px"
            >
              <el-col :xs="8" :sm="8" :md="5" :lg="5" :xl="5">
                <el-cascader
                  v-model="search.from"
                  placeholder="出发地"
                  :options="options"
                  filterable
                  :props="search.pro"
                  :clearable="true"
                  :show-all-levels="false"
                  style="outline: none; border: none; box-shadow: none"
                ></el-cascader>
              </el-col>
              <el-col :xs="8" :sm="8" :md="5" :lg="5" :xl="5">
                <el-cascader
                  v-model="search.to"
                  placeholder="目的地"
                  :options="options"
                  filterable
                  :props="search.pro"
                  :clearable="true"
                  :show-all-levels="false"
                ></el-cascader>
              </el-col>
              <el-col :xs="8" :sm="8" :md="5" :lg="5" :xl="5">
                <el-date-picker
                  v-model="search.date"
                  type="date"
                  placeholder="选择日期"
                  editable
                  style="width: 100%"
                  :clearable="true"
                  value-format="yyyy-MM-dd"
                  :picker-options="limitDate"
                >
                </el-date-picker>
              </el-col>
            </el-row>
            <el-button
              icon="el-icon-search"
              round
              @click="searchFilter"
              type="primary"
              style="
                position: absolute;
                bottom: -20px;
                left: 50%;
                z-index: 2;
                width: 150px;
                transform: translateX(-75px);
              "
              >搜索</el-button
            >
          </el-card>
        </el-col>
      </el-row>
      <el-row type="flex" justify="center" style="margin: 20px 0" v-if="isShow">
        <el-col :xs="20" :sm="18" :md="16" :lg="14" :xl="14">
          <el-card
            shadow="always"
            class="filter-el-card"
            :body-style="{ padding: '5px' }"
          >
            <div
              style="
                display: flex;
                justify-content: end;
                align-items: center;
                height: 100%;
              "
            >
              <el-select
                v-model="listQuery.price"
                placeholder="价格"
                style="width: 120px; margin: 0 10px"
                :clearable="true"
              >
                <el-option
                  v-for="(item, index) in priceOptions"
                  :key="index"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
              <el-select
                v-model="listQuery.time"
                placeholder="耗时"
                style="width: 120px; margin: 0 10px"
                :clearable="true"
              >
                <el-option
                  v-for="(item, index) in timeOptions"
                  :key="index"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
              <el-select
                v-model="listQuery.depart"
                placeholder="出发时间"
                style="width: 140px; margin: 0 10px"
                :clearable="true"
              >
                <el-option
                  v-for="(item, index) in departOptions"
                  :key="index"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
              <el-select
                v-model="listQuery.arrive"
                placeholder="到达时间"
                style="width: 140px; margin: 0 10px"
                :clearable="true"
              >
                <el-option
                  v-for="(item, index) in arriveOptions"
                  :key="index"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
              <el-button
                :circle="true"
                slot="reference"
                class="el-icon-search"
                @click="handlerFilter"
              ></el-button>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <el-row type="flex" justify="center" class="nav" v-if="isShow">
        <el-col
          :xs="20"
          :sm="18"
          :md="16"
          :lg="14"
          :xl="14"
          v-infinite-scroll="loadLimit"
        >
          <div v-for="(train, index) in trainsPart" :key="index">
            <el-card
              shadow="always"
              class="info-el-card"
              :body-style="{ padding: 0 }"
            >
              <el-row
                type="flex"
                justify="space-between"
                align="middle"
                style="height: 120px"
              >
                <el-col :xs="10" :sm="8" :md="10" :lg="10" :xl="10">
                  <el-popover
                    placement="top"
                    title="车次信息"
                    width="300"
                    trigger="hover"
                  >
                    <el-divider></el-divider>
                    <div>
                      <el-timeline>
                        <el-timeline-item
                          :timestamp="
                            train[0].trainLeaveTime.toString().slice(0, 5) +
                            ' ' +
                            train[0].trainArriveCity +
                            ' ' +
                            train[0].trainArriveStation +
                            '火车站 ' +
                            train[0].trainNumberId
                          "
                          placement="top"
                          color="#54c8f4"
                        >
                          <el-card
                            >行程时长{{
                              timeFilter(
                                train[0].trainLeaveTime,
                                train[1].trainArriveTime
                              )
                            }}</el-card
                          >
                        </el-timeline-item>
                        <el-timeline-item
                          :timestamp="
                            train[1].trainArriveTime.toString().slice(0, 5) +
                            ' ' +
                            train[1].trainArriveCity +
                            ' ' +
                            train[1].trainArriveStation +
                            '火车站 ' +
                            train[0].trainNumberId
                          "
                          placement="top"
                          color="#54c8f4"
                        >
                        </el-timeline-item>
                        <el-timeline-item
                          v-if="train.length > 2"
                          :timestamp="
                            train[2].trainLeaveTime.toString().slice(0, 5) +
                            ' ' +
                            train[2].trainArriveCity +
                            ' ' +
                            train[2].trainArriveStation +
                            '火车站 ' +
                            train[0].trainNumberId
                          "
                          placement="top"
                          color="#54c8f4"
                        >
                          <el-card
                            >行程时长{{
                              timeFilter(
                                train[2].trainLeaveTime,
                                train[3].trainArriveTime
                              )
                            }}</el-card
                          >
                        </el-timeline-item>
                        <el-timeline-item
                          v-if="train.length > 2"
                          :timestamp="
                            train[3].trainArriveTime.toString().slice(0, 5) +
                            ' ' +
                            train[3].trainArriveCity +
                            ' ' +
                            train[3].trainArriveStation +
                            '火车站 ' +
                            train[3].trainNumberId
                          "
                          placement="top"
                          color="#54c8f4"
                        >
                        </el-timeline-item>
                      </el-timeline>
                    </div>
                    <div class="row" slot="reference">
                      <div class="col">
                        <h3>{{ train[0].trainArriveCity.slice(0, 2) }}</h3>
                        <div>{{ train[0].trainLeaveTime.slice(0, 5) }}</div>
                      </div>
                      <div class="col">
                        <div
                          v-if="train.length > 2"
                          style="
                            transform: translate(0, 10px);
                            color: #54c8f4;
                            font-size: 18px;
                            font-weight: bold;
                          "
                        >
                          换乘
                        </div>
                        <el-image
                          :src="require('@/assets/arrow.png')"
                          style="margin: 0 10px; width: 80px; height: 30px"
                        >
                        </el-image>
                        <div
                          v-if="compareTime(train)"
                          style="
                            transform: translate(0, -5px);
                            color: #54c8f4;
                            font-size: 15px;
                            font-weight: bold;
                          "
                        >
                          >1天
                        </div>
                      </div>
                      <div class="col">
                        <h3>
                          {{
                            train[
                              typeof train[3] == "object" ? 3 : 1
                            ].trainArriveCity.slice(0, 2)
                          }}
                        </h3>
                        <div>
                          {{
                            train[
                              typeof train[3] == "object" ? 3 : 1
                            ].trainArriveTime.slice(0, 5)
                          }}
                        </div>
                      </div>
                    </div>
                  </el-popover>
                </el-col>
                <el-col :xs="5" :sm="3" :md="3" :lg="3" :xl="3">
                  <h3 class="price" style="color: #409eff; font-size: 25px">
                    {{
                      typeof train[3] == "object"
                        ? train[3].trainOnePrice +
                          train[1].trainOnePrice -
                          train[2].trainOnePrice -
                          train[0].trainOnePrice
                        : train[1].trainOnePrice - train[0].trainOnePrice
                    }}￥
                  </h3>
                </el-col>
                <el-col :xs="4" :sm="3" :md="3" :lg="3" :xl="3">
                  <el-button
                    type="primary"
                    size="medium"
                    class="el-icon-arrow-down"
                    @click="
                      train.show = !train.show;
                      showType(index);
                    "
                    >订购</el-button
                  >
                </el-col>
              </el-row>
            </el-card>
            <el-card v-if="train.show">
              <div class="col">
                <div
                  class="row"
                  style="width: 100%; justify-content: end; margin: 10px 0"
                  v-if="train[1].trainOnePrice > 0"
                >
                  <el-tag type="success" v-if="train.seat.lseat >= 10"
                    >余票充足</el-tag
                  >
                  <el-tag type="warning" v-if="train.seat.lseat < 10"
                    >仅剩 {{ train.seat.lseat }} 张</el-tag
                  >
                  <div class="col">
                    <div style="color: #409eff">硬座</div>
                    <div style="color: #409eff">
                      {{
                        typeof train[3] == "object"
                          ? train[3].trainOnePrice +
                            train[1].trainOnePrice -
                            train[2].trainOnePrice -
                            train[0].trainOnePrice
                          : train[1].trainOnePrice - train[0].trainOnePrice
                      }}￥
                    </div>
                  </div>
                  <button
                    class="payButton"
                    style="margin-left: 20px"
                    @click="order(train, 0)"
                  >
                    预定
                  </button>
                </div>
                <div
                  class="row"
                  style="width: 100%; justify-content: end; margin: 10px 0"
                  v-if="train[1].trainTwoPrice > 0"
                >
                  <el-tag type="success" v-if="train.seat.mseat >= 10"
                    >余票充足</el-tag
                  >
                  <el-tag type="warning" v-if="train.seat.mseat < 10"
                    >仅剩 {{ train.seat.mseat }} 张</el-tag
                  >
                  <div class="col">
                    <div style="color: #409eff">硬卧</div>
                    <div style="color: #409eff">
                      {{
                        typeof train[3] == "object"
                          ? train[3].trainTwoPrice +
                            train[1].trainTwoPrice -
                            train[2].trainTwoPrice -
                            train[0].trainTwoPrice
                          : train[1].trainTwoPrice - train[0].trainTwoPrice
                      }}￥
                    </div>
                  </div>
                  <button
                    class="payButton"
                    style="margin-left: 20px"
                    @click="order(train, 1)"
                  >
                    预定
                  </button>
                </div>
                <div
                  class="row"
                  style="width: 100%; justify-content: end; margin: 10px 0"
                  v-if="train[1].trainSpPrice > 0"
                >
                  <el-tag type="success" v-if="train.seat.hseat >= 10"
                    >余票充足</el-tag
                  >
                  <el-tag type="warning" v-if="train.seat.hseat < 10"
                    >仅剩 {{ train.seat.hseat }} 张</el-tag
                  >
                  <div class="col">
                    <div style="color: #409eff">软座</div>
                    <div style="color: #409eff">
                      {{
                        typeof train[3] == "object"
                          ? train[3].trainSpPrice +
                            train[1].trainSpPrice -
                            train[2].trainSpPrice -
                            train[0].trainSpPrice
                          : train[1].trainSpPrice - train[0].trainSpPrice
                      }}￥
                    </div>
                  </div>
                  <button
                    class="payButton"
                    style="margin-left: 20px"
                    @click="order(train, 2)"
                  >
                    预定
                  </button>
                </div>
                <div
                  class="row"
                  style="width: 100%; justify-content: end; margin: 10px 0"
                  v-if="train[1].trainOneSsPrice > 0"
                >
                  <el-tag type="success" v-if="train.seat.tseat >= 10"
                    >余票充足</el-tag
                  >
                  <el-tag type="warning" v-if="train.seat.tseat < 10"
                    >仅剩 {{ train.seat.tseat }} 张</el-tag
                  >
                  <div class="col">
                    <div style="color: #409eff">软卧</div>
                    <div style="color: #409eff">
                      {{
                        typeof train[3] == "object"
                          ? train[3].trainSspPrice +
                            train[1].trainSspPrice -
                            train[2].trainSspPrice -
                            train[0].trainSspPrice
                          : train[1].trainSspPrice - train[0].trainSspPrice
                      }}￥
                    </div>
                  </div>
                  <button
                    class="payButton"
                    style="margin-left: 20px"
                    @click="order(train, 3)"
                  >
                    预定
                  </button>
                </div>
              </div>
            </el-card>
          </div>
        </el-col>
      </el-row>
      <div
        style="
          display: flex;
          justify-content: center;
          align-items: center;
          position: fixed;
          left: 50%;
          bottom: 20px;
        "
      >
        <div
          style="height: 40px; width: 40px"
          v-loading="isInfiniteLoading"
        ></div>
      </div>
      <div class="loading" v-if="cardLoading"></div>
    </div>

    <el-dialog
      title="订单"
      :visible.sync="dialog"
      append-to-body
      fullscreen
      width="700px"
      customClass="customWidth"
      @open="initOrder"
    >
      <div class="col">
        <el-steps
          :active="active"
          style="width: 900px; background: #fff"
          align-center
        >
          <el-step title="车票信息"></el-step>
          <el-step title="增值服务"></el-step>
          <el-step title="支付"></el-step>
        </el-steps>
        <el-divider></el-divider>
        <div style="width: 100%; height: 700px" v-if="active == 0">
          <div class="row">
            <div class="col">
              <el-card
                style="width: 800px; height: 550px; overflow-y: auto"
                header="乘客"
              >
                <div class="col">
                  <el-card
                    v-for="(passenger, index) in orderInfo.passengers"
                    :key="index"
                    style="
                      width: 100%;
                      height: 150px;
                      margin-bottom: 20px;
                      position: relative;
                    "
                    body-style="padding: 0"
                    shadow="hover"
                  >
                    <div
                      class="row"
                      style="justify-content: start; align-items: start"
                    >
                      <h1 style="margin: 10px 30px; color: #409eff">
                        {{ index }}
                      </h1>
                      <div
                        style="
                          width: 2px;
                          height: 140px;
                          background: #eee;
                          margin: 5px 10px;
                        "
                      ></div>
                      <el-form
                        :model="passenger"
                        label-width="100px"
                        :rules="rules"
                        ref="passengerForm"
                        style="margin-top: 10px"
                      >
                        <el-form-item
                          label="姓名"
                          size="mini"
                          prop="passengerName"
                        >
                          <el-input
                            size="mini"
                            placeholder="乘客姓名"
                            v-model="passenger.passengerName"
                          ></el-input>
                        </el-form-item>
                        <el-form-item
                          label="身份证"
                          size="mini"
                          prop="passengerId"
                        >
                          <el-input
                            size="mini"
                            placeholder="乘客身份证号"
                            v-model="passenger.passengerId"
                          ></el-input>
                        </el-form-item>
                        <el-form-item
                          label="联系电话"
                          size="mini"
                          prop="phoneNumber"
                        >
                          <el-input
                            size="mini"
                            placeholder="乘客电话号码"
                            v-model="passenger.phoneNumber"
                          ></el-input>
                        </el-form-item>
                      </el-form>
                      <el-button
                        class="el-icon-close"
                        type="text"
                        style="
                          font-size: 30px;
                          color: #409eff;
                          position: absolute;
                          right: 10px;
                          top: 0;
                        "
                        @click="deletePassenger(index)"
                      ></el-button>
                    </div>
                  </el-card>
                  <el-button
                    class="el-icon-circle-plus-outline"
                    type="text"
                    style="font-size: 20px; color: #409eff"
                    @click="addPassenger"
                    >增加乘客</el-button
                  >
                </div>
              </el-card>
              <el-card
                style="width: 800px; height: 120px; margin-top: 20px"
                header="联系人"
              >
                <div class="col">
                  <el-form
                    :model="orderInfo.contact"
                    label-width="100px"
                    ref="contactForm"
                    :rules="contactRules"
                  >
                    <el-form-item label="联系电话" size="mini" prop="phone">
                      <el-input
                        size="mini"
                        placeholder="联系人电话号码"
                        style="width: 300px; margin-right: 20px"
                        v-model="orderInfo.contact.phone"
                      ></el-input>
                      <div class="el-icon-warning" style="color: #7c7c7c">
                        订单信息将发送到此手机号
                      </div>
                    </el-form-item>
                  </el-form>
                </div>
              </el-card>
            </div>
            <el-card
              style="width: 400px; height: 700px; margin-left: 20px"
              header="信息"
            >
              <div class="col">
                <h2>
                  {{ search.date + " " + bookTrain.from + "-" + bookTrain.to }}
                </h2>
                <h2>{{ bookTrain.trainId }}</h2>
                <el-divider></el-divider>
                <div
                  v-for="(item, index) in orderInfo.passengers"
                  :key="index"
                  style="width: 100%; margin-bottom: 10px"
                >
                  <div class="col">
                    <div
                      class="row"
                      style="width: 100%; justify-content: space-between"
                    >
                      <div>{{ bookTrain.trainType }}</div>
                      <div>{{ bookTrain.price }}￥</div>
                    </div>
                  </div>
                </div>
                <el-divider></el-divider>
                <h1 style="width: 100%; text-align: right; font-size: 20px">
                  {{ bookTrain.price * orderInfo.passengers.length }}￥
                </h1>
              </div>
            </el-card>
          </div>
        </div>
        <div style="width: 100%; height: 700px" v-if="active == 1">
          <el-alert
            title="增值服务为自助选择业务，不影响您的火车预订，所有增值服务解释权归铁路部门所有。"
            type="warning"
            show-icon
            style="margin-bottom: 20px"
          >
          </el-alert>
          <el-card style="width: 100%; height: 640px" header="增值服务">
            <el-empty description="暂无可用增值服务"></el-empty>
          </el-card>
        </div>
        <div style="width: 100%; height: 700px" v-if="active == 2">
          <el-card
            style="width: 100%; height: 700px; position: relative"
            header="支付"
          >
            <div
              style="display:flex:justify-content:start;align-items:center;flex-direction:column"
            >
              <div style="margin-bottom: 20px; font-size: 18px">支付方式</div>
              <div
                class="display: flex; justify-content: start; align-items: center;"
              >
                <div style="margin-top: 40px; font-size: 16px">移动支付</div>
                <el-divider></el-divider>
                <el-radio-group v-model="paySelect">
                  <el-radio label="支付宝">支付宝</el-radio>
                  <el-radio label="微信">微信</el-radio>
                  <el-radio label="银联">银联</el-radio>
                </el-radio-group>
                <div style="margin-top: 40px; font-size: 16px">银行卡支付</div>
                <el-divider></el-divider>
                <el-radio-group v-model="paySelect">
                  <el-radio label="中国银行">中国银行</el-radio>
                  <el-radio label="工商银行">工商银行</el-radio>
                  <el-radio label="建设银行">建设银行</el-radio>
                  <el-radio label="农业银行">农业银行</el-radio>
                  <el-radio label="交通银行">交通银行</el-radio>
                </el-radio-group>
              </div>
            </div>
            <div
              class="el-icon-warning"
              style="
                color: #9a9b9c;
                position: absolute;
                bottom: 20px;
                right: 20px;
              "
            >
              订单生效起20分钟内可支付，逾期将自动取消
            </div>
            <div v-html="alipay" ref="alipay"></div>
          </el-card>
          <div
            class="row"
            style="
              width: 100%;
              height: 40px;
              justify-content: end;
              margin-top: 20px;
            "
          >
            <h1 style="color: red">总计：</h1>
            <div
              style="
                font-size: 20px;
                font-weight: bold;
                color: #fc9867;
                margin-right: 20px;
              "
            >
              {{ bookTrain.price * orderInfo.passengers.length }}￥
            </div>
            <button @click="pay" class="payButton">立即支付</button>
          </div>
        </div>
        <div class="row" style="margin-top: 10px">
          <el-button
            type="primary"
            @click="last"
            v-if="active > 0 && active < 2"
            style="width: 300px; margin-right: 20px"
            >上一步</el-button
          >
          <el-button
            type="primary"
            @click="next"
            v-if="active < 2"
            style="width: 300px"
            >下一步</el-button
          >
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import constData from "../data/data";
import { mapMutations } from "vuex";
import axios from "axios";
import webconfig from "@/data/config";

export default {
  name: "YTrain",
  data() {
    return {
      limit: 10,
      loading: true,
      cardLoading: false,
      isInfiniteLoading: false,
      options: constData.trainOptions,
      isShow: false,
      trains: [],
      trainsPart: [],
      bookTrain: {
        from: "",
        startTime: "",
        to: "",
        endTime: "",
        trainId: "",
        date: "",
        price: "",
        trainType: "",
      },
      search: {
        from: "",
        to: "",
        date: "",
        pro: {
          expandTrigger: "hover",
        },
      },
      timeOptions: ["耗时升序", "耗时降序"],
      priceOptions: ["价格升序", "价格降序"],
      arriveOptions: ["到达时间升序", "到达时间降序"],
      departOptions: ["出发时间升序", "出发时间降序"],
      listQuery: {
        time: "",
        price: "",
        arrive: "",
        depart: "",
      },
      rules: {
        passengerName: [
          { required: true, message: "请输入姓名", trigger: "blur" },
          {
            min: 2,
            max: 10,
            message: "长度在 2 到 10 个字符",
            trigger: "blur",
          },
        ],
        passengerId: [
          { required: true, message: "请输入身份证号", trigger: "blur" },
          { min: 18, max: 18, message: "长度为 18 个字符", trigger: "blur" },
        ],
        phoneNumber: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { min: 11, max: 11, message: "长度为 11 个字符", trigger: "blur" },
        ],
      },
      contactRules: {
        phone: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { min: 11, max: 11, message: "长度为 11 个字符", trigger: "blur" },
        ],
      },
      dialog: false,
      active: 0,
      orderInfo: {
        passengers: [
          {
            name: "",
            id: "",
            phone: "",
          },
        ],
        contact: {
          phone: "",
        },
      },
      alipay: "",
      paySelect: "",
      paySelect: "",
    };
  },
  methods: {
    ...mapMutations("login", {
      changeLoginDialog: "changeLoginDialog",
    }),
    getPassegers() {
      axios({
        url: `${webconfig.url}/passenger/findPsgByUsrId`,
        headers: { token: localStorage.getItem("token") },
        params: { usrId: this.$store.state.user.userForm.usrId },
      })
        .then((value) => {
          if (value.data.code == 200) {
            this.orderInfo.passengers = value.data.data;
            this.orderInfo.passengers.filter((item) => {
              return item.passengerType == "飞机";
            });
          } else {
            this.$message({
              message: value.data.message,
              type: "error",
              duration: 1000,
            });
          }
        })
        .catch((error) => {
          this.$message({
            message: error,
            type: "error",
            duration: 1000,
          });
        });
    },
    loadLimit() {
      console.log("loadLimit");
      this.limit += 5;
      this.trainsPart = this.trains.slice(0, this.limit);
      this.isInfiniteLoading = true;
      setTimeout(() => {
        this.isInfiniteLoading = false;
      }, 500);
    },
    searchFilter() {
      if (this.search.from == "" || this.search.to == "") {
        this.$message({
          message: "出发地和目的地不能为空",
          type: "error",
          duration: 1000,
        });
        return;
      }
      if (this.search.from[2] == this.search.to[2]) {
        this.$message({
          message: "出发地和目的地不能相同",
          type: "error",
          duration: 1000,
        });
        return;
      }
      if (this.search.date == "") {
        this.$message({
          message: "出发日期不能为空",
          type: "error",
          duration: 1000,
        });
        return;
      }
      this.$refs.headBackground.style.height = "0px";
      this.cardLoading = true;
      this.isShow = false;
      axios({
        url: `${webconfig.url}/train/query-train`,
        method: "post",
        headers: { token: localStorage.getItem("token") },
        data: {
          startCityOrStation: this.search.from[2],
          endCityOrStation: this.search.to[2],
          leaveYearMonthDay: this.search.date,
        },
      })
        .then((value) => {
          if (value.data.code == 200) {
            if (value.data.data.length > 0) {
              console.log(value.data.data);
              this.trains = value.data.data;
              this.trainsPart = this.trains.slice(0, this.limit);
              this.cardLoading = false;
              this.isShow = true;
            } else {
              this.$message({
                message: value.data.message,
                type: "warning",
                duration: 1000,
              });
            }
          } else {
            this.$message({
              message: value.data.message,
              type: "error",
              duration: 1000,
            });
          }
        })
        .catch((error) => {
          this.$message({
            message: error,
            type: "error",
            duration: 1000,
          });
        });
      if (this.cardLoading == true) {
        setTimeout(() => {
          this.cardLoading = false;
        }, 800);
      }
    },
    handlerFilter() {
      if (this.listQuery.price != "") {
        if (this.listQuery.price == "价格升序") {
          this.trains.sort((a, b) => {
            return (
              a[a.length - 1].trainOnePrice -
              a[0].trainOnePrice -
              (b[b.length - 1].trainOnePrice - b[0].trainOnePrice)
            );
          });
        } else if (this.listQuery.price == "价格降序") {
          this.trains.sort((a, b) => {
            return (
              b[b.length - 1].trainOnePrice -
              b[0].trainOnePrice -
              (a[a.length - 1].trainOnePrice - a[0].trainOnePrice)
            );
          });
        }
      }
      if (this.listQuery.time != "") {
        if (this.listQuery.time == "耗时升序") {
          this.trains.sort((a, b) => {
            return (
              Date.parse(
                `1900-01-01 ${a[a.length - 1].trainArriveTime
                  .toString()
                  .slice(0, 5)}`
              ) -
              Date.parse(
                `1900-01-01 ${a[0].trainLeaveTime.toString().slice(0, 5)}`
              ) -
              (Date.parse(
                `1900-01-01 ${b[b.length - 1].trainArriveTime
                  .toString()
                  .slice(0, 5)}`
              ) -
                Date.parse(
                  `1900-01-01 ${b[0].trainLeaveTime.toString().slice(0, 5)}`
                ))
            );
          });
        } else if (this.listQuery.time == "耗时降序") {
          this.trains.sort((a, b) => {
            return (
              Date.parse(
                `1900-01-01 ${b[b.length - 1].trainArriveTime
                  .toString()
                  .slice(0, 5)}`
              ) -
              Date.parse(
                `1900-01-01 ${b[0].trainLeaveTime.toString().slice(0, 5)}`
              ) -
              (Date.parse(
                `1900-01-01 ${a[a.length - 1].trainArriveTime
                  .toString()
                  .slice(0, 5)}`
              ) -
                Date.parse(
                  `1900-01-01 ${a[0].trainLeaveTime.toString().slice(0, 5)}`
                ))
            );
          });
        }
      }
      if (this.listQuery.arrive != "") {
        if (this.listQuery.arrive == "到达时间升序") {
          this.trains.sort((a, b) => {
            return (
              Date.parse(
                `1900-01-01 ${a[a.length - 1].trainArriveTime
                  .toString()
                  .slice(0, 5)}`
              ) -
              Date.parse(
                `1900-01-01 ${b[b.length - 1].trainArriveTime
                  .toString()
                  .slice(0, 5)}`
              )
            );
          });
        } else if (this.listQuery.arrive == "到达时间降序") {
          this.trains.sort((a, b) => {
            return (
              Date.parse(
                `1900-01-01 ${b[b.length - 1].trainArriveTime
                  .toString()
                  .slice(0, 5)}`
              ) -
              Date.parse(
                `1900-01-01 ${a[a.length - 1].trainArriveTime
                  .toString()
                  .slice(0, 5)}`
              )
            );
          });
        }
      }
      if (this.listQuery.depart != "") {
        if (this.listQuery.depart == "出发时间升序") {
          this.trains.sort((a, b) => {
            return (
              Date.parse(
                `1900-01-01 ${a[0].trainLeaveTime.toString().slice(0, 5)}`
              ) -
              Date.parse(
                `1900-01-01 ${b[0].trainLeaveTime.toString().slice(0, 5)}`
              )
            );
          });
        } else if (this.listQuery.depart == "出发时间降序") {
          this.trains.sort((a, b) => {
            return (
              Date.parse(
                `1900-01-01 ${b[0].trainLeaveTime.toString().slice(0, 5)}`
              ) -
              Date.parse(
                `1900-01-01 ${a[0].trainLeaveTime.toString().slice(0, 5)}`
              )
            );
          });
        }
      }
      this.trainsPart = this.trains.slice(0, this.limit);
    },
    order(row, type) {
      if (this.$store.state.login.loginState == false) {
        this.changeLoginDialog();
        this.$message({
          message: "请先登录",
          type: "warning",
        });
      } else {
        this.active = 0;
        this.dialog = true;
        this.getPassegers();
        this.bookTrain.from = row[0].trainArriveCity.slice(0, 2);
        this.bookTrain.startTime = row[0].trainLeaveTime;
        this.bookTrain.to = row[
          typeof row[3] == "object" ? 3 : 1
        ].trainArriveCity.slice(0, 2);
        this.bookTrain.endTime =
          row[typeof row[3] == "object" ? 3 : 1].trainArriveTime;
        this.bookTrain.trainId = row[0].trainNumberId;
        this.bookTrain.date = this.search.date;
        if (typeof row[3] != "object") {
          switch (type) {
            case 0:
              this.bookTrain.price =
                row[1].trainOnePrice - row[0].trainOnePrice;
              this.bookTrain.trainType = "硬座";
              break;
            case 1:
              this.bookTrain.price =
                row[1].trainTwoPrice - row[0].trainTwoPrice;
              this.bookTrain.trainType = "硬卧";
              break;
            case 2:
              this.bookTrain.price = row[1].trainSpPrice - row[0].trainSpPrice;
              this.bookTrain.trainType = "软座";
              break;
            case 3:
              this.bookTrain.price =
                row[1].trainSspPrice - row[0].trainSspPrice;
              this.bookTrain.trainType = "软卧";
              break;
          }
        } else {
          switch (type) {
            case 0:
              this.bookTrain.price =
                row[3].trainOnePrice +
                row[1].trainOnePrice -
                row[0].trainOnePrice -
                row[2].trainOnePrice;
              this.bookTrain.trainType = "硬座";
              break;
            case 1:
              this.bookTrain.price =
                row[3].trainTwoPrice +
                row[1].trainTwoPrice -
                row[0].trainTwoPrice -
                row[2].trainTwoPrice;
              this.bookTrain.trainType = "硬卧";
              break;
            case 2:
              this.bookTrain.price =
                row[3].trainSpPrice +
                row[1].trainSpPrice -
                row[0].trainSpPrice -
                row[2].trainSpPrice;
              this.bookTrain.trainType = "软座";
              break;
            case 3:
              this.bookTrain.price =
                row[3].trainSspPrice +
                row[1].trainSspPrice -
                row[0].trainSspPrice -
                row[2].trainSspPrice;
              this.bookTrain.trainType = "软卧";
              break;
          }
        }
      }
    },
    next() {
      if (this.active < 3) {
        if (this.active == 0) {
          if (this.orderInfo.passengers.length == 0) {
            this.$message({
              message: "请添加乘客",
              type: "warning",
            });
            return;
          }
          let judge = true;
          console.log(this.$refs);
          this.$refs.passengerForm.forEach((item) => {
            console.log(item);
            item.validate((valid) => {
              if (!valid) {
                judge = false;
              }
            });
          });
          if (!judge) {
            this.$message({
              message: "请填写正确的乘客信息",
              type: "warning",
            });
            return;
          } else {
            this.$refs.contactForm.validate((valid) => {
              if (valid) {
                this.addPassengers();
                this.active++;
              } else {
                this.$message({
                  message: "请填写联系人信息",
                  type: "warning",
                });
              }
            });
          }
        } else if (this.active == 1) {
          this.active++;
        } else if (this.active == 2) {
          this.active++;
        }
      }
    },
    last() {
      if (this.active > 0) {
        this.active--;
      }
    },
    initOrder() {
      this.orderInfo = {
        passengers: [
          {
            name: "",
            id: "",
            phone: "",
          },
        ],
        contact: {
          phone: "",
        },
      };
    },
    addPassenger() {
      let passenger = {
        name: "",
        id: "",
        phone: "",
      };
      this.orderInfo.passengers.push(passenger);
    },
    addPassengers() {
      axios({
        method: "post",
        url: `${webconfig.url}/passenger/addPassengerList`,
        headers: { token: localStorage.getItem("token") },
        data: this.orderInfo.passengers,
      });
    },
    deletePassenger(index) {
      this.orderInfo.passengers.splice(index, 1);
    },
    pay() {
      if (this.paySelect == "支付宝") {
        const data = {
          vehicleType: "火车",
          flightOrTrainNO: this.bookTrain.trainId,
          startPortOrStation: this.bookTrain.from,
          endPortOrStation: this.bookTrain.to,
          startTime:
            this.search.date +
            " " +
            this.bookTrain.startTime.toString().slice(0, 5),
          endTime:
            this.search.date +
            " " +
            this.bookTrain.endTime.toString().slice(0, 5),
          passengerNames: this.orderInfo.passengers.map((item) => {
            return item.passengerName;
          }),
          seatType: this.bookTrain.trainType,
          money: this.bookTrain.price * this.orderInfo.passengers.length,
          usrId: this.$store.state.user.userForm.usrId,
        };
        axios({
          method: "post",
          url: `${webconfig.url}/usr/alipay`,
          data: data,
        })
          .then((res) => {
            this.alipay = res.data.data;
            console.log(this.$refs.alipay);
            this.$nextTick(() => {
              this.$refs.alipay.children[0].submit();
            });
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        this.$message({
          message: "暂不支持改支付方式",
          type: "warning",
        });
      }
    },
    timeFilter(from, to) {
      let startTime = Date.parse(`1900-01-01 ${from.toString().slice(0, 5)}`);
      let endTime = Date.parse(`1900-01-01 ${to.toString().slice(0, 5)}`);
      let diff = endTime - startTime;

      let hours = Math.floor(diff / 3600000);
      let minutes = Math.floor((diff - hours * 3600000) / 60000);
      let seconds = Math.floor(
        (diff - hours * 3600000 - minutes * 60000) / 1000
      );

      return `${hours.toString().padStart(2, 0)}:${minutes
        .toString()
        .padStart(2, 0)}:${seconds.toString().padStart(2, 0)}`;
    },
    compareTime(info) {
      let startTime1 = Date.parse(
        `1900-01-01 ${info[0].trainLeaveTime.toString().slice(0, 5)}`
      );
      let endTime1 = Date.parse(
        `1900-01-01 ${info[1].trainArriveTime.toString().slice(0, 5)}`
      );
      let startTime2 = "";
      let endTime2 = "";
      if (info.length > 2) {
        startTime2 = Date.parse(
          `1900-01-01 ${info[2].trainLeaveTime.toString().slice(0, 5)}`
        );
        endTime2 = Date.parse(
          `1900-01-01 ${info[3].trainArriveTime.toString().slice(0, 5)}`
        );
      }
      if (startTime1 > endTime1 || startTime2 > endTime2) {
        return true;
      }
    },
    showType(index) {
      axios({
        method: "post",
        url: `${webconfig.url}/train/query-train-seat`,
        headers: { token: localStorage.getItem("token") },
        data: {
          trainNumberId:
            this.trains[index][this.trains[index].length - 1].trainNumberId,
          day: this.search.date,
          startStation: this.search.from[2],
          endStation: this.search.to[2],
        },
      }).then((res) => {
        console.log(res.data.data);
        let seat = res.data.data;
        this.$nextTick(() => {
          this.trains[index] = {
            ...this.trains[index],
            seat: {
              lseat: seat[0],
              mseat: seat[1],
              hseat: seat[2],
              tseat: seat[3],
            },
          };
          this.trainsPart = this.trains.slice(0, this.limit);
        });
      });
    },
  },
  computed: {
    limitDate() {
      return {
        disabledDate: (time) => {
          // time为当前日期, disabledDate自带参数
          return (
            time.getTime() < Date.now() - 1 * 24 * 60 * 60 * 1000 ||
            time.getTime() > Date.now() + 6 * 24 * 60 * 60 * 1000
          );
        },
      };
    },
  },
  activated() {
    // this.trainsPart = this.trains.slice(0, this.limit);
    setTimeout(() => {
      this.$refs.headBackground.style.height = "400px";
      this.loading = false;
    }, 500);
  },
  deactivated() {
    this.loading = true;
    this.isShow = false;
  },
};
</script>

<style scoped>
.search-nav {
  margin: 30px 0;
}
.search-nav-el-card {
  height: 100px;
  border-radius: 10px;
}
.search-nav-el-card:hover {
  box-shadow: 0 2px 15px 1px rgba(0, 0, 0, 0.2);
}
